.paginator {
  margin: 15px;
}
.pageNumber {
  font-family: cursive;
  padding: 10px;
  border-radius: 30px;
  border: 2px solid rgb(248, 17, 67);
}

.pageNumber.selectedPage {
  font-weight: bold;
  border-color: rgb(11, 160, 219);
}

.button {
  margin: 20px;
  border: 3px solid rgb(223, 238, 8);
  border-radius: 7px;
  font-family: cursive;
}
